<template>
	<div>
		<van-nav-bar
		  fixed
		  border
		  title="客户列表"
		  left-arrow
		  @click-left="goBack"
		/>
		<van-tabs v-model="activeTabIndex">
		  <van-tab :title="`意向客户(${list.length})`" >
			  <van-row type="flex" justify="space-around" class="box" v-for="(item,index) in list" :key="index">
			    <van-col span="22" class="tab_content">
					<div class="tab_info">
						<p><i class="iconfont iconyixiangren"></i>意向人：{{item.username}}（邀请人：{{item.inviter}}）</p>
						<p><i class="iconfont iconcontact"></i>联系电话：{{item.phone}}</p>
						<p><i class="iconfont iconshijian"></i>登记时间：{{item.datetime}}</p>
					</div>
					<van-row  type="flex"  class="info_btn">
					  <van-col span="10" class="lx" @click="callPhoneMark(item)" v-if="item.is_contract==0">马上联系</van-col>
					  <van-col span="10" class="lx" style="background-color:#7F7F7F;" v-else  @click="callPhoneTow(item.phone)">已联系</van-col>
					</van-row>
				</van-col>
			  </van-row>
		  </van-tab>
		  <van-tab :title="`访客(${VisitLog.length})`" >
			  <van-row type="flex" justify="space-around" class="box" v-for="(item,index) in VisitLog" :key="index">
			    <van-col span="22" class="tab_content">
					<div class="tab_info">
						<p><i class="iconfont iconyixiangren"></i>访客：{{item.username}}（邀请人：{{item.inviter}}）</p>
						<p><i class="iconfont iconcontact"></i>联系电话：{{item.phone}}</p>
						<p><i class="iconfont iconshijian"></i>访问时间：{{item.datetime}}</p>
					</div>
					<van-row  type="flex"  class="info_btn">
					  <van-col span="10" class="lx"  @click="callPhone(item)" v-if="item.is_contract == 0">马上联系</van-col>
					  <van-col span="10" class="lx"  v-else @click="callPhoneTow(item.phone)" style="background-color: #7F7F7F;">已联系</van-col>
					</van-row>
				</van-col>
			  </van-row>
		  </van-tab>
		</van-tabs>
	</div>
</template>

<script>
import {Tabs,Tab,Col, Row,NavBar} from "vant";	
import {agentContractUserList,visitMarkContract,agentHouseVisitLog,markContract} from "@/api/v4.js"

export default{
	components:{
		[NavBar.name]: NavBar,
		[Tabs.name]: Tabs,
		[Tab.name]: Tab,	
		[Col.name]: Col,
		[Row.name]: Row,
	},
	data() {
	  return {
	    activeTabIndex: 0,
		list:[],
		VisitLog:[]
	  }
	},
	created() {
		this.getData()
	},
	methods:{
		goBack() {
			this.$router.back();
		},
		callPhone (item) {
			console.log(item)
		    window.location.href = 'tel://' + item.phone
			visitMarkContract({
				record_id:item.record_id
			}).then(res=>{
				this.getData()
			})
		},
		callPhoneMark (item) {
			console.log(item)
		    window.location.href = 'tel://' + item.phone
			markContract({
				record_id:item.record_id
			}).then(res=>{
				this.getData()
			})
		},
		callPhoneTow(phone){
			window.location.href = 'tel://' + phone
			this.getData()
		},
		getData(){
			let house_id = this.$route.params.id
			agentContractUserList({house_id}).then(res=>{
				console.log(res)
				this.list=res
			})
			agentHouseVisitLog({house_id}).then(res=>{
				this.VisitLog=res
			})
		}
	},
}
</script>

